
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import {mapGetters, mapState, mapMutations} from "vuex";
import Dict from "../../components/dict/Dict";
import UserSelect from "../../components/User/template";
import {backMixin} from "../../common/js/picBack";
import dayjs from "dayjs";

export default {
name: "NewOrder",
mixins: [backMixin],
components: {
UserSelect,
XHeader,
Group,
XInput,
Cell,
FormPreview,
Datetime,
Dict,
PopupPicker,
XTextarea,
XButton,
Previewer
},
props: {
workRu: {
type: Boolean,
default: true
},
width: {
default: 85
}
},
data() {
return {
options: {
saveImg(el) {
}
},
orderno: "自动生成",
payment: [
["未付", "支票", "网上银行", "电付", "邮局汇款", "支付宝", "其他"]
],
payMethod: ["未付"],
form: {
Title: "",
BeginDate: dayjs(new Date()).format("YYYY-MM-DD"),
EndDate: "",
DealDate: dayjs(new Date()).format("YYYY-MM-DD"),
PayType: "",
OrderState: [],
Remark: "",
TotalPrice: 0,
CustState: [],
CustStateName: ""
},
productList: [],
defaultChoose: [],
imgFile: [],
CustomerID: "",
CustomerName: "请选择客户",
LinkUserID: [],
cLinkman: [],
LinkUser: [],
isUserShow: false,
areyouok: true,
isSelectCust: true,
parentPath: "",
userShow: false,
checkVal: [],
senders: [],
sendersName: '',
goBackAndClean: true
};
},
computed: {
...mapGetters(["getChooseProductData", "getChooseCustomer"]),
...mapState(["chooseProductData", "chooseCustomer", "chooseLinkMan"])
},
filters: {

picUrl(path, w = 0, h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
}
},
mounted() {
this.$bus.on("selectedClient", data => {
this.CustomerID = data.ID;
this.CustomerName = data.CustomerName;
this.LinkUserID = data.LinkID;
this.form.CustState[0] = data.CustState;
this.form.CustStateName = data.CustStateName;
});
if (this.$route.query.cId) {
this.CustomerID = this.$route.query.cId;
this.isSelectCust = false;
this.getLinkman();
} else {
this.isSelectCust = true;
}
if (this.$route.query.cName) {
this.CustomerName = this.$route.query.cName;
}
if (this.$route.query.cLink) {
this.LinkUserID = this.$route.query.cLink;
}
if (this.$route.query.cCustStateName) {
this.form.CustState[0] = this.$route.query.cState;
this.form.CustStateName = this.$route.query.cCustStateName;
}
this.parentPath = this.$route.path;
},
activated() {
if (this.$route.query.cId) {
this.CustomerID = this.$route.query.cId;
this.isSelectCust = false;
this.getLinkman();
} else {
this.isSelectCust = true;
}
if (this.$route.query.cName) {
this.CustomerName = this.$route.query.cName;
}
if (this.$route.query.cLink) {
this.LinkUserID = this.$route.query.cLink;
}
if (this.$route.query.cCustStateName) {
this.form.CustState[0] = this.$route.query.cState;
this.form.CustStateName = this.$route.query.cCustStateName;
}
this.parentPath = this.$route.path;
},
destroyed() {

},
beforeRouteLeave(to, fromb, next) {
if ((to.fullPath == '/' || to.fullPath.indexOf('client') != -1) && this.goBackAndClean) {
let _this = this
this.$vux.confirm.show({
title: '继续退出？',
content: "您已编辑过，退出将丢失这些内容！",
onCancel() {
next(false)
},
onConfirm() {
_this.form.Title = "";
_this.setChooseProduct([]);
_this.form.EndDate = "";
_this.$set(_this.payMethod, 0, _this.payment[0][0]);
_this.form.CustState = [];
_this.form.Remark = "";
_this.form.OrderState = [];
_this.form.TotalPrice = 0;
_this.imgFile = [];
_this.CustomerName = "请选择客户";
_this.CustomerID = "";
_this.LinkUserID = [];
_this.cLinkman = [];
_this.LinkUser = [];
_this.form.CustStateName = "";
_this.$refs.OrderState.cleanValue();
_this.$refs.CustState.cleanValue();
_this.sendersName = ''
_this.senders = []
next()
}
});
} else {
next()
}
},
methods: {
...mapMutations([
"setChooseCustomer",
"setChooseLinkMan",
"setChooseProduct"
]),

userSelected(list) {
this.senders = []
this.sendersName = ''
list.forEach(item => {
this.senders.push({
UserID: item.ID,
IsDelete: 0,
ID: 0
})
if (list.length < 2) {
this.sendersName += item.OrgName
} else {
this.sendersName += item.OrgName + ','
}
})
this.checkVal = list.map(val => {
return {
UserID: val.ID,
Name: val.OrgName
};
});
this.userShow = false
},
formatTotalPrice() {
this.form.TotalPrice = parseFloat(
(this.form.TotalPrice + "").replace(/[^\d.]/g, "")
).toFixed(2);
},

changeDealNum(e, item, index) {
this.$store.state.chooseProductData.forEach((item1, index1) => {
if (item.ID == item1.ID) {
this.$store.state.chooseProductData[index1].DealNum = parseInt(e.target.value)
}
})
},
getTotalPrice() {
let price = 0;
this.productList.forEach(item => {
if (item.DealNum == "" || isNaN(item.DealNum)) {
item.DealNum = 0;
}
price += item.DealPrice * parseInt(item.DealNum);
});
this.form.TotalPrice = price.toFixed(2);
},
beforeShow() {
if (!this.CustomerID) {
this.$vux.toast.show({
type: "warn",
text: "请先选择客户"
});
this.isUserShow = false;
return false;
} else {
this.isUserShow = true;
}
},
saveOrder() {
this.form.TotalPrice = parseFloat(
(this.form.TotalPrice + "").replace(/[^\d.]/g, "")
).toFixed(2);
let PayType = "";
this.payment[0].some((item, index) => {
if (item === this.payMethod[0]) {
PayType = index + 1 + "";
return true;
}
});

if (!this.areyouok) {
return;
}
this.areyouok = false;
if (!this.CustomerID) {

this.$vux.toast.show({
type: "warn",
text: "请选择客户"
});
this.areyouok = true;
return;
}
if (this.LinkUser.length === 0) {

this.$vux.toast.show({
type: "warn",
text: "请选择联系人"
});
this.areyouok = true;
return;
}
if (this.chooseProductData.length == 0) {

this.$vux.toast.show({
type: "warn",
text: "请选择产品"
});
this.areyouok = true;
return;
}
if (this.form.DealDate == "") {

this.$vux.toast.show({
type: "warn",
text: "请选择签约时间"
});
this.areyouok = true;
return;
}
if (this.form.BeginDate == "") {

this.$vux.toast.show({
type: "warn",
text: "请选择开始时间"
});
this.areyouok = true;
return;
}
if (this.form.EndDate == "") {

this.$vux.toast.show({
type: "warn",
text: "请选择结束时间"
});
this.areyouok = true;
return;
}

if (dayjs(this.form.BeginDate).unix() > dayjs(this.form.EndDate).unix()) {

this.$vux.toast.show({
type: "warn",
text: "开始时间不能大于结束时间"
});
this.areyouok = true;
return;
}







if (this.form.OrderState === "" || this.form.OrderState == undefined) {

this.$vux.toast.show({
type: "warn",
text: "请选择订单状态"
});
this.areyouok = true;
return;
}

if (this.form.CustState === "" || this.form.CustState == undefined) {

this.$vux.toast.show({
type: "warn",
text: "请选择客户状态"
});
this.areyouok = true;
return;
}

let Prods = [];
let ProdOK = true;
let ProdMore = true;
this.chooseProductData.forEach(item => {
if (parseInt(item.DealNum) <= 0 || item.DealNum == "") {
ProdOK = false;
}
if (parseInt(item.DealNum) >= 10000000) {
ProdMore = false;
}
Prods.push({
ID: 0,
OrderProdID: item.ID,
DealNum: item.DealNum,
DealPrice: item.DealPrice,
Remark: item.Remark
});
});

if (!ProdOK) {

this.$vux.toast.show({
type: "warn",
text: "产品数量必须大于0"
});
this.areyouok = true;
return;
}
if (!ProdMore) {

this.$vux.toast.show({
type: "warn",
text: "产品数量必须小于10000000"
});
this.areyouok = true;
return;
}

if (parseFloat(this.form.TotalPrice) <= 0) {

this.$vux.toast.show({
type: "warn",
text: "成交总额必须大于0"
});
this.areyouok = true;
return;
}
if (parseFloat(this.form.TotalPrice) >= 10000000000) {

this.$vux.toast.show({
type: "warn",
text: "成交总额必须小于10000000000"
});
this.areyouok = true;
return;
}

let order = {
ID: 0,
IsAutoNumber: 1,
CustomerState: this.form.CustState.ID,
Title: this.form.Title,
Remark: this.form.Remark,
ClientID: this.CustomerID,
CustomerID: this.CustomerID,
LinkUserID: this.LinkUser[0],
CustomerName: this.CustomerName,
BeginDate: this.form.BeginDate + " 00:00:00",
EndDate: this.form.EndDate + " 00:00:00",
DealDate: this.form.DealDate + " 00:00:00",
PayType: PayType,
OrderNumber: "",
TotalPrice: this.form.TotalPrice,
OrderState: this.form.OrderState.ID,
Prods: Prods,
ImgAttach: this.imgFile,
Senders: this.senders
};


axios
.post(`/Order/SaveOrder`, order)
.then(res => {
if (res.data.State === 1) {
this.$vux.toast.show({
text: "保存成功",
type: "success"
});
let sendArr = []
this.senders.forEach(item => {
sendArr.push(item.UserID)
})
if (sendArr.length > 0) {
this.send(sendArr, res.data.Data)
}
this.goBackAndClean = false
this.$bus.emit("onSave", true);
this.$bus.emit("refreshOrderList")
setTimeout(() => {
this.form.Title = "";
this.setChooseProduct([]);
this.form.DealDate = "";
this.form.BeginDate = "";
this.form.EndDate = "";
this.payMethod[0] = this.payment[0][0];

this.form.CustState = [];
this.form.Remark = "";
this.form.OrderState = [];
this.form.TotalPrice = 0;
this.imgFile = [];
this.CustomerName = "请选择客户";
this.CustomerID = "";
this.LinkUserID = [];
this.cLinkman = [];
this.LinkUser = [];
this.form.CustStateName = "";
this.$refs.OrderState.cleanValue();
this.$refs.CustState.cleanValue();
this.$router.back();
this.areyouok = true;
this.$vux.toast.hide();
}, 500);
} else {
this.areyouok = true;
this.$vux.toast.show({
text: res.data.ErrorMessage,
type: "warn"
});
}
})
.catch(e => {
this.$vux.toast.show({
text: "网络异常",
type: "warn"
});
});
},

send(userID, id) {
userID.forEach((item, index) => {
if (item == this.$store.state.CurrentUserID) {
userID.splice(index, 1)
}
})
let obj = {
SendUsers: userID,
EvName: "crm-order",
ImgPath: `${down}/mdtcrm/CRMOrder.jpg`,
Title: "订单",
Content: "您有一个新的订单",
Params: [id]
};
this.postAxios(`${api50}/api/v1/Common/SendSingleEventMsg`, obj).then(
res => {
console.log(res);
}
);
},      //发送消息
send(userID, id) {
userID.forEach((item, index) => {
if (item == this.$store.state.CurrentUserID) {
userID.splice(index, 1)
}
})
let obj = {
SendUsers: userID,
EvName: "crm-order",
ImgPath: `${down}/mdtcrm/CRMOrder.jpg`,
Title: "订单",
Content: "您有一个新的订单",
Params: [id]
};
this.postAxios(`${api50}/api/v1/Common/SendSingleEventMsg`, obj).then(
res => {
console.log(res);
}
);
},
showUser() {
if (this.chooseCustomer.ID) {
this.$router.push(`/selectLinkman?id=${this.chooseCustomer.ID}`);
}
},

selectPic() {
if (this.imgFile.length >= 5) {
this.$vux.toast.show({
type: "warn",
text: "最多上传五张"
});
return false;
}
plus.gallery.pick(path => {
path.files.map(item => {
this.updated(item, 1)
.then(res => {
this.$vux.loading.hide();
let obj = res.Data[0];
obj.FileSize = res.FileSize;
obj.src = `${api50}/api/v1/File/DownLoadPic?filePath=${
obj.FilePath
}`;
this.imgFile.push(obj);
})
.catch(err => {
this.$vux.loading.hide();
let msg = err.ErrorMessage || "上传失败";
this.$vux.toast.text(msg);
});
})
}, () => {
}, {filter: "image", multiple: true, maximum: 5 - this.imgFile.length, system: false});
},


updated(path, type) {
return new Promise((resolve, reject) => {
const url = this.BillFileUrl(type);
let uploader = plus.uploader.createUpload(
url,
{method: "POST", blocksize: 1024 * 1024 * 10},
upload => {
let res = JSON.parse(upload.responseText);
if (res.State == 1) {
res.FileSize = upload.totalSize;
resolve(res);
} else {
reject(res);
}
}
);
let fileName = path.substr(
path.lastIndexOf("/") + 1,
path.length - path.lastIndexOf("/")
);
uploader.addFile(path, {key: fileName});
let index = 0;
uploader.addEventListener(
"statechanged",
(upload, status) => {
if (type != 2 && upload.totalSize > 1024 * 1024 * 10) {
this.$vux.toast.show({
type: "warn",
text: "图片大小不超过10M"
});
uploader.abort();
return false;
} else {
if (index == 0) {
this.$vux.loading.show({
text: "上传中"
});
}
index++;
}
},
false
);
uploader.start();
});
},
saveImg(val) {
},
delPic(index) {
let _this = this;
this.$vux.confirm.show({
content: "确定删除该图片吗？",
onConfirm() {
_this.imgFile.splice(index, 1);
}
});
},
imgShow(index) {
this.$refs.previewer.show(index);
},
delProduct(index) {
this.chooseProductData.splice(index, 1);
},

getLinkman() {
return new Promise(resolve => {
this.getAxios(`/Customer/GetPagedCustomerLinkManList`, {
page: 1,
count: 100,
customerId: this.CustomerID
}).then(res => {
this.cLinkman = [];
let list = [];
res.Data.List.forEach(item => {
list.push({
value: `${item.ID}`,
name: item.LinkName
});
});
this.cLinkman.push(list);
this.LinkUser = [`${this.LinkUserID}`];
resolve();
});
});
}
},
watch: {
chooseProductData: {
deep: true,
handler: function (val) {
this.defaultChoose = [];
if (val.length !== 0) {
val.forEach(item => {
this.defaultChoose.push(item.ID);
if (parseInt(item.DealNum) <= 0) {
item.DealNum = 1;
}
item.DealNum = parseInt(item.DealNum);
});
this.productList = val;
this.setChooseProduct(val);
this.getTotalPrice();
}
}
},
productList: {
handler: function (val) {
this.getTotalPrice();
},
deep: true
},
CustomerID() {
if (this.CustomerID) {
this.getLinkman();
}
},
"form.TotalPrice"(val) {

}







}
};



